import React, { Component } from 'react';
import { Col } from 'reactstrap';
import InfoPage from 'components/Templates/InfoPage/InfoPage';
import Loadable from 'react-loadable';

// import LandingBlogContainer from 'containers/Landing/LandingBlogContainer';
// import LandingPromoContainer from 'containers/Landing/LandingPromoContainer';
// import LandingSplash from './LandingSplash/LandingSplash';
// import LandingCategories from './LandingCategories/LandingCategories';
// import LandingInsurerCarousel from './LandingInsurerCarousel/LandingInsurerCarousel';
// import LandingMAS from './LandingMAS/LandingMAS';
// import LandingApp from './LandingApp/LandingApp';
// import LandingBusiness from './LandingBusiness/LandingBusiness';
// import LandingTestimonial from './LandingTestimonial/LandingTestimonial';

import './Landing.scss';

const LandingBlogContainer = Loadable({
  loader: () => import(/* webpackChunkName: "LandingBlogContainer" */ 'containers/Landing/LandingBlogContainer'),
  loading: () => null,
  modules: ['LandingBlogContainer']
});

const LandingPromoContainer = Loadable({
  loader: () => import(/* webpackChunkName: "LandingPromoContainer" */ 'containers/Landing/LandingPromoContainer'),
  loading: () => null,
  modules: ['LandingPromoContainer']
});

const LandingSplash = Loadable({
  loader: () => import(/* webpackChunkName: "LandingSplash" */ './LandingSplash/LandingSplash'),
  loading: () => null,
  modules: ['LandingSplash']
});

const LandingCategories = Loadable({
  loader: () => import(/* webpackChunkName: "LandingCategories" */ './LandingCategories/LandingCategories'),
  loading: () => null,
  modules: ['LandingCategories']
});

const LandingInsurerCarousel = Loadable({
  loader: () => import(/* webpackChunkName: "LandingInsurerCarousel" */ './LandingInsurerCarousel/LandingInsurerCarousel'),
  loading: () => null,
  modules: ['LandingInsurerCarousel']
});

const LandingMAS = Loadable({
  loader: () => import(/* webpackChunkName: "LandingMAS" */ './LandingMAS/LandingMAS'),
  loading: () => null,
  modules: ['LandingMAS']
});

const LandingApp = Loadable({
  loader: () => import(/* webpackChunkName: "LandingApp" */ './LandingApp/LandingApp'),
  loading: () => null,
  modules: ['LandingApp']
});

const LandingBusiness = Loadable({
  loader: () => import(/* webpackChunkName: "LandingBusiness" */ './LandingBusiness/LandingBusiness'),
  loading: () => null,
  modules: ['LandingBusiness']
});

const LandingTestimonial = Loadable({
  loader: () => import(/* webpackChunkName: "LandingTestimonial" */ './LandingTestimonial/LandingTestimonial'),
  loading: () => null,
  modules: ['LandingTestimonial']
});


class Landing extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isScrolled: false,
    };

    this.onMovement = this.onMovement.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    setTimeout(() => {
      if (window.scrollY > 0) {
        this.onMovement();
      } else {
        this.timeout = setTimeout(this.onMovement, 10000);
        window.addEventListener('scroll', this.onMovement);
      }
      
    }, 1000);
  }

  onMovement() {
    const { isScrolled } = this.state;
    const { getBlogData, doGetAllPromoGiveaways } = this.props;

    if (!isScrolled) {
      this.setState({
        isScrolled: true,
      });

      getBlogData();
      doGetAllPromoGiveaways();

      clearTimeout(this.timeout);
      window.removeEventListener('scroll', this.onMovement);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onMovement);
  }

  render() {
    return (
      <InfoPage
        className="landing-info-page"
        noAutoMargin
        mode="full"
        upperBG
      >
        <Col className="landing-header-container" xs={12}>
          <LandingSplash />
          <div className="d-none d-lg-flex justify-content-center align-items-center">
            <LandingCategories />
          </div>
          <div className="d-none d-lg-block info-page-margin">
            <LandingInsurerCarousel />
          </div>
        </Col>
        <Col className="d-flex d-lg-none justify-content-center" xs={12}>
          <LandingCategories />
        </Col>
        <Col className="d-block d-lg-none" xs={12}>
          <LandingInsurerCarousel />
        </Col>
        <Col xs={12}>
          <LandingMAS />
        </Col>
        <Col className="info-page-margin" xs={12}>
          <LandingApp />
        </Col>
        <Col xs={12}>
          <LandingBusiness />
        </Col>
        <Col className="info-page-margin" xs={12}>
          <LandingPromoContainer />
        </Col>
        <Col className="info-page-margin" xs={12}>
          <LandingBlogContainer />
        </Col>
        <Col className="info-page-margin" xs={12}>
          <LandingTestimonial />
        </Col>
      </InfoPage>
    );
  }
}

export default Landing;
